.menupage{
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--textWhite);
    height: 100%;
    background-image: linear-gradient( rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.2) ),  url(../../assets/images/dark-blue.jpg);
    background-position: center;
    background-size: cover;
    padding-top: 100px;


    .location{
        .go-to-menu-btn{
            width: fit-content;    
            .custom-button{
                background-color: #04397e;
                transition-duration:  1s;
                p{
                    letter-spacing: 2.5px;
                    margin: 0;
                    font-weight: 600;
                }
                &:hover {
                    background-color: rgb(12, 11, 11);
                    color: #c5c5c5;
                    }
            }
        }
    }
}
.menupage-text{
    margin: 20px 0 0 0;
    width: 100%;
    padding: 40px 10%;
    text-align: center;
    color: var(--textWhite);
    

    h1{
        font-weight: 600;
        line-height: 28px;
        font-family: 'Poppins',Helvetica,Arial,Lucida,sans-serif;
        font-size: 18px;
        letter-spacing: 2px;

    }

}

.menupage-concept{
    width: 100%;
    margin: 30px 0px 80px 0px;
    padding: 40px 20%;
    text-align: center;
    color: var(--textWhite);
    font-weight: 300;
    line-height: 28px;
}

@media screen and (max-width:500px){

    .menupage{
        background-image: linear-gradient( rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.6) ),  url(../../assets/images/dark-blue.jpg);
        background-position: center;
        background-size: auto;

    }

    .menupage-text{
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 0;
        height: 90px;
    }

    .menupage-concept{
        width: 90%;
        margin: 0 0px 80px 0px;
        padding: 20px 0;
        text-align: left;
}


}